@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

:root {
  --primary-color-hue: 252;
  --dark-color-lightness: 17%;
  --light-color-lightness: 95%;
  --white-color-lightness: 100%;

  --color-white: hsl(252, 30%, var(--white-color-lightness));
  --color-light: hsl(252, 30%, var(--light-color-lightness));
  --color-gray: hsl(var(--primary-color-hue), 15%, 65%);
  --color-primary: hsl(var(--primary-color-hue), 75%, 60%);
  --color-secondary: hsl(252, 100%, 90%);
  --color-success: hsl(120, 95%, 65%);
  --color-danger: hsl(0, 95%, 65%);
  --color-dark: hsl(252, 30%, var(--dark-color-lightness));
  --color-black: hsl(252, 30%, 10%);

  --border-radius: 2rem;
  --card-border-radius: 1rem;
  --btn-padding: 0.6rem 2rem;
  --search-padding: 0.6rem 1rem;
  --card-padding: 1rem;

  --sticky-top-left: 5.4rem;
  --sticky-top-right: -18rem;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  border: none;
}

::-webkit-scrollbar {
  display: none;
}
a{
  color: #000;
}

body {
  font-family: "Poppins", sans-serif;
  color: var(--color-dark);
  background: var(--color-light);
  overflow-x: hidden;
}

/* GENERAL STYLES */
.container {
  width: 80%;
  margin: 0 auto;
}

.profile-photo {
  width: 2.7rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

img {
  display: block;
  width: 100%;
}

.btn {
  display: inline-block;
  padding: var(--btn-padding);
  font-weight: 500;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all 300ms ease;
  font-size: 0.9rem;
}

.btn:hover {
  opacity: 0.8;
}

.btn-primary {
  background: var(--color-primary);
  color: white;
}

.text-bold {
  font-weight: 500;
}

.text-muted {
  color: var(--color-gray);
}

/*================== NAVBAR =================*/
nav {
  width: 100%;
  background: rgb(243, 213, 45);
  padding: 0.7rem 0;
  position: fixed;
  top: 0;
  z-index: 10;
}

nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-bar {
  background: var(--color-light);
  border-radius: var(--border-radius);
  padding: var(--search-padding);
}

.search-bar input[type="search"] {
  background: transparent;
  width: 30vw;
  margin-left: 1rem;
  font-size: 0.9rem;
  color: var(--color-dark);
}

nav .search-bar input[type="search"]::placeholder {
  color: var(--color-gray);
}

nav .create {
  display: flex;
  align-items: center;
  gap: 2rem;
}

nav .create #menu-btn {
  display: none;
}

/* ===================== MAIN ======================= */
main {
  position: relative;
  top: 5.4rem;
}

main .container {
  display: grid;
  grid-template-columns: 18vw auto 20vw;
  column-gap: 2rem;
  position: relative;
}

/* =================== LEFT ================ */
main .container .left {
  /* height: max-content; */
  height: 100vh;
  /* position: sticky;
  top: var(--sticky-top-left); */
}

main .container .left .profile {
  padding: var(--card-padding);
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  display: flex;
  align-items: center;
  column-gap: 1rem;
  width: 100%;
}

/* -------------- SIDEBAR ------------------ */
.left .sidebar {
  margin-top: 1rem;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
}

.left #close-btn {
  display: none;
}

.left .sidebar .menu-item {
  display: flex;
  align-items: center;
  height: 4rem;
  cursor: pointer;
  transition: all 300ms ease;
  position: relative;
}

.left .sidebar .menu-item:hover {
  background: var(--color-light);
}

.left .sidebar i {
  font-size: 1.4rem;
  color: var(--color-gray);
  margin-left: 2rem;
  position: relative;
}

.left .sidebar i .notification-count {
  background: var(--color-danger);
  color: white;
  font-size: 0.7rem;
  width: fit-content;
  border-radius: 0.8rem;
  padding: 0.1rem 0.4rem;
  position: absolute;
  top: -0.2rem;
  right: -0.3rem;
}

.left .sidebar h3 {
  margin-left: 1.5rem;
  font-size: 1rem;
}

.left .sidebar .active {
  background: var(--color-light);
}

.left .sidebar .active i,
.left .sidebar .active h3 {
  color: var(--color-primary);
}

.left .sidebar .active::before {
  content: "";
  display: block;
  width: 0.5rem;
  height: 100%;
  position: absolute;
  background: var(--color-primary);
}

.left .sidebar .menu-item:first-child.active {
  border-top-left-radius: var(--card-border-radius);
}

.left .sidebar .menu-item:last-child.active {
  border-bottom-left-radius: var(--card-border-radius);
  overflow: hidden;
}

.left .btn {
  margin-top: 1rem;
  width: 100%;
  text-align: center;
  padding: 1rem 0;
}

/* ----------------------- NOTIFICATION POPUP ---------------- */
.left .notifications-popup {
  position: absolute;
  top: 0;
  left: 110%;
  width: 30rem;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  box-shadow: 0 0 2rem rgba(0, 0, 0, 0.1);
  z-index: 8;
  display: none;
}

.left .notifications-popup::before {
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  display: block;
  background: var(--color-white);
  position: absolute;
  left: -0.6rem;
  transform: rotate(45deg);
}

.left .notifications-popup>div {
  display: flex;
  align-items: start;
  gap: 1rem;
  margin-bottom: 1rem;
}

.left .notifications-popup small {
  display: block;
}

/* ============================= MIDDLE ================================ */
.middle .stories {
  display: flex;
  height: 12rem;
  max-width: 500px;
  overflow-x: scroll;
  gap: 0.5rem;
}

.middle .stories .story {
  padding: var(--card-padding);
  border-radius: var(--card-border-radius);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-size: 0.75rem;
  min-width: 100px;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}



.middle .stories .story .name {
  z-index: 2;
}


.middle .story .profile-photo {
  width: 2rem;
  height: 2rem;
  align-self: start;
  border: 3px solid var(--color-primary);
}

.middle .myStory {
  max-width: 100px;
}

.storyAdd {
  cursor: pointer;
  color: #fff;
  font-size: 2rem;
}

.storyPic {
  position: absolute;
  z-index: 7;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
}

.userStory {
  padding: 1rem 1.8rem !important;
  width: 80%;
}

.userStory .storyPic img {
  width: 100%;
  height: 100%;
}

.myStory .storyPic img {
  /* display: none; */
  width: 100%;
  object-fit: cover;
  height: 100%;
}


#storiesContainer {
  display: flex;
  gap: 0.5rem;
}

.viewStory{
  position: fixed;
  top: 0;
  left: 35%;
  margin: auto;
  z-index: 9;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin: auto;

}
.viewStory img{
  width: 400px;
  height: 100vh;
  object-fit: contain;
}

/* ----------------- CREATE POST ------------- */
.middle .create-post {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
  background: var(--color-white);
  padding: 0.4rem var(--card-padding);
  border-radius: var(--border-radius);
}

.middle .create-post input[type="text"] {
  justify-self: start;
  width: 100%;
  padding-left: 1rem;
  background: transparent;
  color: var(--color-dark);
  margin-right: 1rem;
}

/* ---------------------- FEEDS --------------------- */
.middle .feeds .feed {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin: 1rem 0;
  font-size: 0.85rem;
  line-height: 1.5;
}

.middle .feed .head {
  display: flex;
  justify-content: space-between;
}

.middle .feed .user {
  display: flex;
  align-items: center;
  gap: 1rem;
  cursor: pointer;
}

.postContent {
  margin-top: 1rem;
}

.postContent p {
  font-size: 1rem;
}

.createPost {
  z-index: 10;
  /* display: none; */
  flex-direction: column;
  position: fixed;
  height: 70%;
  left: 25%;
  right: 25%;
  top: 15%;
  bottom: 0%;
  background: #fff;
  border-radius: 1rem;
  border: 1px solid rgb(214, 214, 214);
  box-shadow: 0 6px 24px 0 rgba(18, 43, 70, .1);
}

.createPostHead {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid rgb(214, 214, 214);
}


.userHead {
  width: 100%;
  height: 50px;
  aspect-ratio: 1/1;
  margin-top: 1rem;
  margin-left: 1rem;
  display: flex;
  gap: 1rem;
}

.userHead img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.userHead h4 {
  align-self: center;
}

#close-create-post {
  cursor: pointer;
}

.contentArea {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 250px;
  overflow-y: auto;
}

.contentArea textarea {
  width: 100%;
  height: 100%;
  padding: 1rem;
  border: none;
  outline: none;
  resize: none;
  font-size: 1rem;
  min-height: 200px;
}

.contentArea textarea::placeholder {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: lighter;
  color: rgb(212, 212, 212);
}

#imgContainer {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}


.sendSection {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  /* height: 5rem; */
  border-top: 1px solid rgb(214, 214, 214);

}

.mediaUploader {
  display: none;
}

.sendSection i {
  font-size: 1.5rem;
  cursor: pointer;
  font-weight: bolder;
}

.sendSection .create {
  padding: 5px 1rem;
  border-radius: 1rem;
  cursor: pointer;
  background: var(--color-primary);
  color: #fff;
  font-weight: 500;
}

.sendSection .attach .create {
  position: absolute;
  right: 10%;
}


.middle .feed .photo {
  border-radius: var(--card-border-radius);
  overflow: hidden;
  margin: 0.7rem 0;
  width: 100%;
  height: 350px;
}

.middle .feed .photo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.middle .feed .action-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.4rem;
  margin: 0.6rem;
}

.middle .liked-by {
  display: flex;
}

.middle .liked-by span {
  width: 1.4rem;
  height: 1.4rem;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--color-white);
  margin-left: -0.6rem;
}

.middle .liked-by span:first-child {
  margin: 0;
}

.middle .liked-by p {
  margin-left: 0.5rem;
}

/* ========================== RIGHT =============================== */
main .container .right {
  height: max-content;
}

.right .messages {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
}

.right .messages .heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.right .messages i {
  font-size: 1.4rem;
}

.right .messages .search-bar {
  display: flex;
  margin-bottom: 1rem;
}

.right .messages .category {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.right .messages .category h6 {
  width: 100%;
  text-align: center;
  border-bottom: 4px solid var(--color-light);
  padding-bottom: 0.5rem;
  font-size: 0.85rem;
}

.right .messages .category .active {
  border-color: var(--color-dark);
}

.right .messages .message-requests {
  color: var(--color-primary);
}

.right .messages .message {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  align-items: start;
}

.right .message .profile-photo {
  position: relative;
  overflow: visible;
}

.right .profile-photo img {
  border-radius: 50%;
}

.right .messages .message:last-child {
  margin: 0;
}

.right .messages .message p {
  font-size: 0.8rem;
}

.right .messages .message .profile-photo .active {
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 50%;
  border: 3px solid var(--color-white);
  background: var(--color-success);
  position: absolute;
  bottom: 0;
  right: 0;
}

/* ================= FRIEND REQUESTS =================== */
.right .friend-requests {
  margin-top: 1rem;
}

.right .friend-requests h4 {
  color: var(--color-gray);
  margin: 1rem 0;
}

.right .request {
  background: var(--color-white);
  padding: var(--card-padding);
  border-radius: var(--card-border-radius);
  margin-bottom: 0.7rem;
}

.right .request .info {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.right .request .action {
  display: flex;
  gap: 1rem;
}

/* ======================== THEME CUSTOMIZATION ========================== */
.customize-theme {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  text-align: center;
  display: grid;
  place-items: center;
  font-size: 0.9rem;
  display: none;
}

.customize-theme .card {
  background: var(--color-white);
  padding: 3rem;
  border-radius: var(--card-border-radius);
  width: 50vw;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
}

/* ----------------- FONT SIZE ------------------ */
.customize-theme .font-size {
  margin-top: 5rem;
}

.customize-theme .font-size>div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-light);
  padding: var(--search-padding);
  border-radius: var(--card-border-radius);
}

.customize-theme .choose-size {
  background: var(--color-secondary);
  height: 0.3rem;
  width: 100%;
  margin: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.customize-theme .choose-size span {
  width: 1rem;
  height: 1rem;
  background: var(--color-secondary);
  border-radius: 50%;
  cursor: pointer;
}

.customize-theme .choose-size span.active {
  background: var(--color-primary);
}

/* ----------------- FONT SIZE ------------------ */
.customize-theme .color {
  margin-top: 2rem;
}

.customize-theme .choose-color {
  background: var(--color-light);
  padding: var(--search-padding);
  border-radius: var(--card-border-radius);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.customize-theme .choose-color span {
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 50%;
}

.customize-theme .choose-color span:nth-child(1) {
  background: hsl(252, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(2) {
  background: hsl(52, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(3) {
  background: hsl(352, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(4) {
  background: hsl(152, 75%, 60%);
}

.customize-theme .choose-color span:nth-child(5) {
  background: hsl(202, 75%, 60%);
}

.customize-theme .choose-color span.active {
  border: 5px solid white;
}

/* -------------------- BACKGROUND -------------------- */
.customize-theme .background {
  margin-top: 2rem;
}

.customize-theme .choose-bg {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
}

.customize-theme .choose-bg>div {
  padding: var(--card-padding);
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.4rem;
  cursor: pointer;
}

.customize-theme .choose-bg>div.active {
  border: 2px solid var(--color-primary);
}

.customize-theme .choose-bg .bg-1 {
  background: white;
  color: black;
}

.customize-theme .choose-bg .bg-2 {
  background: hsl(252, 30%, 17%);
  color: white;
}

.customize-theme .choose-bg .bg-3 {
  background: hsl(252, 30%, 10%);
  color: white;
}

.customize-theme .choose-bg>div span {
  width: 2rem;
  height: 2rem;
  border: 2px solid var(--color-gray);
  border-radius: 50%;
  margin-right: 1rem;
}

/* ==========================
MEDIA QUERIES FOR SMALL LAPTOPS AND BIG TABLETS
==========================
*/
@media screen and (max-width: 1200px) {
  .container {
    width: 96%;
  }

  nav .create #menu-btn {
    font-size: 1.6rem;
    display: inline-block;
  }

  main .container {
    grid-template-columns: auto 30vw;
    gap: 1rem;
  }

  .left {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background: var(--color-white);
    width: 18rem;
    box-shadow: 0 1rem 2rem 2rem rgba(0, 0, 0, 0.1);
    display: none;
  }

  .left #close-btn {
    display: inline-block;
    position: absolute;
    z-index: 2;
    top: 1rem;
    right: 1rem;
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    transition: all 300ms ease;
    cursor: pointer;
  }

  .left #close-btn:hover {
    background: rgba(0, 0, 0, 0.1);
    border: 1px solid var(--color-dark);
  }

  .left .sidebar {
    margin: 5rem 0 0 0;
    position: relative;
  }

  .left #close-btn i {
    color: var(--color-dark);
  }

  .left .sidebar .menu-item {
    height: 4.4rem;
  }

  main .container .left .profile {
    display: none;
  }

  .left .btn {
    display: none;
  }

  .customize-theme .card {
    width: 80vw;
  }
}

.miniSearch {
  display: none;
}

/* ==========================
MEDIA QUERIES FOR SMALL TABLETS AND MOBILE PHONES
==========================
*/
@media screen and (max-width: 992px) {
  nav .search-bar {
    display: none;
  }

  nav .create label {
    display: none;
  }

  .miniSearch {
    margin-top: 0.5rem;
    border: 1px solid gray;
    display: flex;
  }

  main .container {
    grid-template-columns: auto 0;
    gap: 0;
  }

  /* ------------ NOTIFICATION POPUP ------------- */
  .left .notifications-popup {
    position: absolute;
    width: 60vw;
    font-size: 80%;
    left: 80%;
  }

  /* -------------- hide last two stories on mobile ------------ */
  main .middle .stories .story:nth-child(5),
  main .middle .stories .story:nth-child(6) {
    display: none;
  }


  .customize-theme .card {
    width: 92vw;
    padding: 1rem;
  }

  .customize-theme .choose-bg>div {
    width: 90%;
  }

  .customize-theme .choose-bg>div span {
    width: 1rem;
    height: 1rem;
    border: 2px solid var(--color-gray);
    border-radius: 50%;
    margin-right: 1rem;
  }
}

.cloudinary-button {
  background: none;
}

.floatingStory {
  z-index: 999090;
  position: fixed;
  inset: 25% 35%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0.8rem;
  background: #fff;
  border-radius: 1rem;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 1rem;
  width: 25%;
}
.floatingStory button{
  padding: 0.5rem;
  border-radius: 0.5rem;
 cursor: pointer; 
}
.floatingStory img{
  width: 70%;
}
.closeStory{
  position: absolute;
  width: 100%;
  left: 45%;
  top: 5%;
  cursor: pointer;
}



.storyBackLay{
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 8;
}

.closeStoryViewer{
  cursor: pointer;
  position: absolute;
  right: 1%;
  color: #fff;
  z-index: 10;
  top: 10%;
}

/* Profile Section */


.profileContainer {
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  font-size: 0.85rem;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 1rem;
}

.profileContainer .upper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}
.profilePhoto{
  position: relative;
  overflow: hidden;
}
.editPhoto{
  position: absolute;
  bottom: 0%;
  right: 15%;
  z-index: 9;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}
.editPhoto i{
  color: #fff;
  font-size: 1.5rem;
}

.upper img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid rgb(243, 213, 45);
}

.lower {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 5rem;
}

.lower .userDetails {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}

.lower .userDetails>div {
  display: flex;
  gap: 5px;
  margin: auto;
}

.buttonArea .follow {
  background: #595999;
  color: #fff;
  padding: 0.5rem 1rem;
  height: fit-content;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 0.5px;
}
.editProfileBtn{
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border: 2px solid #595999;
  background: transparent;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: 0.4s ease-in-out;
}
.editProfileBtn:hover{
  background: #595999;
  color: #fff;
}
.btnFollowing{
  background: #afafaf !important;
}

.buttonArea .threeDot {
  cursor: pointer;
  font-size: 1.2rem;
  margin-left: 1.5rem;
  background: transparent;
  padding: 0.5rem 1rem;
  border: 1px solid #6c6c6c;
  border-radius: 0.5rem;
  transition: 0.4s ease-in-out;
}

.buttonArea .threeDot:hover {
  border: 1px solid #fff;
  background: #595999;
  color: #fff;
}

.followList {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
}

.profileContainer .nameProfile {
  font-weight: bold;
  font-size: 1.5rem;
}

.profileHref {
  cursor: pointer;
}

.postContent {
  margin-top: 1rem;
}

.userOwnPosts{
  margin-top: 1rem;
}
.divider{
  margin-top: 1rem;
  height: 2px;
  background: gray;
}
.search-bar{
  position: relative;
}
.searchResult{
    position: absolute;
    left: 0;
    top: 105%;
    border-radius: 2rem;
    background: #fff;
    padding: 1rem;
    display: flex;
    align-items: center;
    gap: 2rem;
    width: 100%;
    box-shadow: 0px 2px 5px gray;
}
.searchResult .uil-times-circle{
  position: absolute;
  right: 5%;
  font-size: 1.5rem;
  cursor: pointer;
}


/* Message Box */
.messageBox{
  display: flex;
  align-items: center;
  flex-direction: column;
  background: #fff;
  border-radius: 1rem;
  min-height: 85vh;
}
.msgHeader{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0.5rem 1rem;
}
.msgHeader i{
  font-size: 1.5rem;
  cursor: pointer;
}
.msgInput{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem;
  border-top: 1px solid rgb(214, 214, 214);
}
#msgInput{
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  width: 100%;
}
.msgInput span{
  cursor: pointer;
  font-size: 1.5rem;
}
.msgArea{
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  align-self: baseline;
  height: 65vh;
  overflow-y: auto;
  background: #ccccccad;
}
.msgArea p{
  width: fit-content;
  padding: 5px 0.5rem;
  border-radius: 0.75rem;  
  margin-block: 5px;
  max-width: 75%;
}
.me{
  background: lightcoral;
  align-self: flex-end;
}
.you{
  background: lightblue;
  align-self: flex-start;
}
.message{
  padding: 0.5rem;
  border-radius: 0.8rem;
  cursor: pointer;
}
.message .profile-photo img{
  width: 40px;
  height: 40px;
}
.message.active{
  background: #6B4DE6;
  color: #fff;

}


.empty{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  font-size: 1rem;
  font-weight: bold;
  font-weight: lighter;
  color: #595999;
}
.empty i{
  font-size: 3rem;
}
.noUser h2{
  font-size: 1rem;
  font-weight: lighter;
}



.mobileNav {
  display: none;
  position: fixed;
  bottom: 0%;
  left: 0;
  padding: 0.25rem 1.2rem;
  width: 100%;
  background: #f4f4f5;
  z-index: 10000000000000000;
}

.mobileNav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobileNav ul li a {
  margin-block: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  color: #27272a;
  font-size: 0.78rem;
}

.sell a svg {
  border-radius: 50%;
  border: 4px solid var(--secondary);
  padding: 0.2rem;
  position: absolute;
  top: -15%;
  background: #f4f4f5;
}

.sell a p {
  margin-block: 0.3rem;
  margin-top: 2.5rem;
}



@media (max-width: 450px) {
  .mobileNav {
      display: block;
  }
  main .container .right{
    margin-top: 2rem;
    margin-bottom: 5rem;
  }
  .createPost{
    right: 5%;
    left: 5%;
  }
  .viewStory{
    width: 100%;
    left: 0%;
  }
  .active {
      color: var(--secondary) !important;
  }
}